	module.exports = {"data":{"postsJson":{"title":"Paul Arrested in Jerusalem","questionsTitle":"OPENING THE WORD","display":"block","bookName":"Acts","ref":"21:17-23:11","html":"<p>   <strong>21</strong><u>17</u>When we had come to Jerusalem, the brothers received us gladly. <u>18</u>The day following, Paul went in with us to James; and all the elders were present. <u>19</u>When he had greeted them, he reported one by one the things which God had worked among the Gentiles through his ministry. <u>20</u>They, when they heard it, glorified God. They said to him,</p><h1>JAMES BROTHER OF JESUS &amp; THE ELDERS OF THE CHURCH</h1><h4>You see, brother, how many thousands there are among the Jews of those who have believed, and they are all zealous for the law. <u>21</u>They have been informed about you, that you teach all the Jews who are among the Gentiles to forsake Moses, telling them not to circumcise their children neither to walk after the customs. <u>22</u>What then? The assembly must certainly meet, for they will hear that you have come. <u>23</u>Therefore do what we tell you. We have four men who have taken a vow. <u>24</u>Take them, and purify yourself with them, and pay their expenses for them, that they may shave their heads. Then all will know that there is no truth in the things that they have been informed about you, but that you yourself also walk keeping the law. <u>25</u>But concerning the Gentiles who believe, we have written our decision that they should observe no such thing, except that they should keep themselves from food offered to idols, from blood, from strangled things, and from sexual immorality.</h4><p>   <u>26</u>Then Paul took the men, and the next day, purified himself and went with them into the temple, declaring the fulfillment of the days of purification, until the offering was offered for every one of them. <u>27</u>When the seven days were almost completed, the Jews from Asia, when they saw him in the temple, stirred up all the multitude and laid hands on him, <u>28</u>crying out,</p><h1>JEWS FROM ASIA</h1><h4>Men of Israel, help! This is the man who teaches all men everywhere against the people, and the law, and this place. Moreover, he also brought Greeks into the temple, and has defiled this holy place!</h4><p><u>29</u>For they had seen Trophimus, the Ephesian, with him in the city, and they supposed that Paul had brought him into the temple.<br />   <u>30</u>All the city was moved, and the people ran together. They seized Paul and dragged him out of the temple. Immediately the doors were shut. <u>31</u>As they were trying to kill him, news came up to the commanding officer of the regiment that all Jerusalem was in an uproar. <u>32</u>Immediately he took soldiers and centurions, and ran down to them. They, when they saw the chief captain and the soldiers, stopped beating Paul. <u>33</u>Then the commanding officer came near, arrested him, commanded him to be bound with two chains, and inquired who he was and what he had done. <u>34</u>Some shouted one thing, and some another, among the crowd. When he couldn’t find out the truth because of the noise, he commanded him to be brought into the barracks.<br />   <u>35</u>When he came to the stairs, he was carried by the soldiers because of the violence of the crowd; <u>36</u>for the multitude of the people followed after, crying out,</p><h1>A MOB IN JERUSALEM</h1><h4>Away with him!</h4><p><u>37</u>As Paul was about to be brought into the barracks, he asked the commanding officer,</p><h1>PAUL</h1><h3>May I speak to you?</h3><p>   He said,</p><h1>CLAUDIUS LYSIAS</h1><h4>Do you know Greek? <u>38</u>Aren’t you then the Egyptian, who before these days stirred up to sedition and led out into the wilderness the four thousand men of the Assassins?</h4><p>   <u>39</u>But Paul said,</p><h1>PAUL</h1><h3>I am a Jew, from Tarsus in Cilicia, a citizen of no insignificant city. I beg you, allow me to speak to the people.</h3><p>   <u>40</u>When he had given him permission, Paul, standing on the stairs, beckoned with his hand to the people. When there was a great silence, he spoke to them in the Hebrew language, saying,</p><h1>PAUL</h1><h3> <strong>22</strong><u>1</u>Brothers and fathers, listen to the defense which I now make to you.</h3><p>   <u>2</u>When they heard that he spoke to them in the Hebrew language, they were even more quiet. He said,</p><h1>PAUL</h1><h3><u>3</u>I am indeed a Jew, born in Tarsus of Cilicia, but brought up in this city at the feet of Gamaliel, instructed according to the strict tradition of the law of our fathers, being zealous for God, even as you all are today. <u>4</u>I persecuted this Way to the death, binding and delivering into prisons both men and women. <u>5</u>As also the high priest and all the council of the elders testify, from whom also I received letters to the brothers, and traveled to Damascus to bring them also who were there to Jerusalem in bonds to be punished. <u>6</u>As I made my journey, and came close to Damascus, about noon, suddenly a great light shone around me from the sky. <u>7</u>I fell to the ground, and heard a voice saying to me, ‘Saul, Saul, why are you persecuting me?’ <u>8</u>I answered, ‘Who are you, Lord?’ He said to me, ‘I am Jesus of Nazareth, whom you persecute.’<br />   <u>9</u>Those who were with me indeed saw the light and were afraid, but they didn’t understand the voice of him who spoke to me. <u>10</u>I said, ‘What shall I do, Lord?’ The Lord said to me, ‘Arise, and go into Damascus. There you will be told about all things which are appointed for you to do.’ <u>11</u>When I couldn’t see for the glory of that light, being led by the hand of those who were with me, I came into Damascus. <u>12</u>One Ananias, a devout man according to the law, well reported of by all the Jews who lived in Damascus, <u>13</u>came to me, and standing by me said to me, ‘Brother Saul, receive your sight!’ In that very hour I looked up at him. <u>14</u>He said, ‘The God of our fathers has appointed you to know his will, and to see the Righteous One, and to hear a voice from his mouth. <u>15</u>For you will be a witness for him to all men of what you have seen and heard. <u>16</u>Now why do you wait? Arise, be baptized, and wash away your sins, calling on the name of the Lord.’<br />   <u>17</u>When I had returned to Jerusalem, and while I prayed in the temple, I fell into a trance, <u>18</u>and saw him saying to me, ‘Hurry and get out of Jerusalem quickly, because they will not receive testimony concerning me from you.’ <u>19</u>I said, ‘Lord, they themselves know that I imprisoned and beat in every synagogue those who believed in you. <u>20</u>When the blood of Stephen, your witness, was shed, I also was standing by, and consenting to his death, and guarding the cloaks of those who killed him.’<br />   <u>21</u>He said to me, ‘Depart, for I will send you out far from here to the Gentiles.’</h3><p>   <u>22</u>They listened to him until he said that; then they lifted up their voice, and said,</p><h1>A MOB IN JERUSALEM</h1><h4>Rid the earth of this fellow, for he isn’t fit to live!</h4><p>   <u>23</u>As they cried out, and threw off their cloaks, and threw dust into the air, <u>24</u>the commanding officer commanded him to be brought into the barracks, ordering him to be examined by scourging, that he might know for what crime they shouted against him like that. <u>25</u>When they had tied him up with thongs, Paul asked the centurion who stood by,</p><h1>PAUL</h1><h3>Is it lawful for you to scourge a man who is a Roman, and not found guilty?</h3><p>   <u>26</u>When the centurion heard it, he went to the commanding officer and told him,</p><h1>A ROMAN OFFICER</h1><h4>Watch what you are about to do, for this man is a Roman!</h4><p>   <u>27</u>The commanding officer came and asked him,</p><h1>CLAUDIUS LYSIAS</h1><h4>Tell me, are you a Roman?</h4><p>   He said,</p><h1>PAUL</h1><h3>Yes.</h3><p>   <u>28</u>The commanding officer answered,</p><h1>CLAUDIUS LYSIAS</h1><h4>I bought my citizenship for a great price.</h4><p>   Paul said,</p><h1>PAUL</h1><h3>But I was born a Roman.</h3><p>   <u>29</u>Immediately those who were about to examine him departed from him, and the commanding officer also was afraid when he realized that he was a Roman, because he had bound him. <u>30</u>But on the next day, desiring to know the truth about why he was accused by the Jews, he freed him from the bonds, and commanded the chief priests and all the council to come together, and brought Paul down and set him before them.<br />   <strong>23</strong><u>1</u>Paul, looking steadfastly at the council, said,</p><h1>PAUL</h1><h3>Brothers, I have lived before God in all good conscience until today.</h3><p>   <u>2</u>The high priest, Ananias, commanded those who stood by him to strike him on the mouth.<br />   <u>3</u>Then Paul said to him,</p><h1>PAUL</h1><h3>God will strike you, you whitewashed wall! Do you sit to judge me according to the law, and command me to be struck contrary to the law?</h3><p>   <u>4</u>Those who stood by said,</p><h1>JEWISH LEADERS</h1><h4>Do you malign God’s high priest?</h4><p>   <u>5</u>Paul said,</p><h1>PAUL</h1><h3>I didn’t know, brothers, that he was high priest. For it is written, ‘You shall not speak evil of a ruler of your people.’*</h3><p><u>6</u>But when Paul perceived that the one part were Sadducees and the other Pharisees, he cried out in the council,</p><h1>PAUL</h1><h3>Men and brothers, I am a Pharisee, a son of Pharisees. Concerning the hope and resurrection of the dead I am being judged!</h3><p>   <u>7</u>When he had said this, an argument arose between the Pharisees and Sadducees, and the assembly was divided. <u>8</u>For the Sadducees say that there is no resurrection, nor angel, nor spirit; but the Pharisees confess all of these. <u>9</u>A great clamor arose, and some of the scribes of the Pharisees part stood up, and contended, saying,</p><h1>THE PHARISEES</h1><h4>We find no evil in this man. But if a spirit or angel has spoken to him, let’s not fight against God!</h4><p>   <u>10</u>When a great argument arose, the commanding officer, fearing that Paul would be torn in pieces by them, commanded the soldiers to go down and take him by force from among them, and bring him into the barracks.<br />   <u>11</u>The following night, the Lord stood by him, and said,</p><h1>GOD</h1><h2>Cheer up, Paul, for as you have testified about me at Jerusalem, so you must testify also at Rome.</h2><h5>*Acts 23:5 Exodus 22:28</h5>","path":"/361","Question1":"What do you like about this portion of Scripture?","Question2":"What do you observe about the ways of God?","Question3":"What do you observe about the key characters in this portion of Scripture?","Question4":"What response are you wanting to make in light of these observations?","prevPath":"/360","nextPath":"/362"}},"pathContext":{}}

